// A reusable carousel component
/**
 * Creates a carousel using the Swiper library
 * @param {string} selector - CSS selector for the carousel container
 * @param {Object} options - Custom options for the carousel
 */
export function createCarousel(selector, options = {}) {
  // Import Swiper dynamically
  import('swiper').then(({ default: Swiper, Navigation, Pagination }) => {
    // Import Swiper CSS
    import('swiper/css');
    import('swiper/css/navigation');
    import('swiper/css/pagination');

    // Default options
    const defaultOptions = {
      modules: [Navigation, Pagination],
      slidesPerView: 1,
      spaceBetween: 30,
      loop: true,
      navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
      },
      pagination: {
        el: '.swiper-pagination',
        clickable: true,
      },
    };

    // Create and initialize the carousel
    new Swiper(selector, { ...defaultOptions, ...options });
  });
}
